import React, { useState, useEffect } from "react";
import "../App.css";
import {
  COLOR_DARK,
  COLOR_DIVISOR,
  COLOR_HIGHLIGHTED,
  COLOR_LIGHT,
  COLOR_MEDIUM,
  COLOR_VERYLIGHT,
  FONT,
  increaseProgress,
  REPEATS,
  ROMAN_FONT,
  ROMAN_SYSTEM,
} from "../defaults";
import {
  InputBoxCheck,
  levelCorrect,
  levelNoInput,
  level0RomanTask1,
  level0RomanTask2,
  levelIncorrect,
  nextLevelUnlocked,
  Tutorial0Continue,
  buttonContinue,
  buttonCheck,
} from "../strings";
import { convertToAlternate, convertToDecimal } from "../helpers";
import Coin from "../components/Coin";
import coin_I from "../images/coin_I.png";
import coin_V from "../images/coin_V.png";
import coin_X from "../images/coin_X.png";
import coin_L from "../images/coin_L.png";
import coin_C from "../images/coin_C.png";
import coin_D from "../images/coin_D.png";
import coin_M from "../images/coin_M.png";
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5toTouch } from "rdndmb-html5-to-touch";

const LevelNumber = 0;

function generateNumber(currentProgress) {
  const numbers = [8, 43, 122];
  if (currentProgress >= numbers.length) {
    return Math.ceil(Math.random() * 998) + 1; // Generate integer between 1 and 999. These are optional but slightly harder, because the numbers are larger.
  }
  return numbers[currentProgress];
}

const containerStyle = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  fontFamily: FONT,
  fontSize: "3dvh",
  padding: "0",
  borderRadius: "0.25em",
  //boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  width: "6dvh",
  margin: "0 auto",
  justifyContent: "center",
  //backgroundColor: COLOR_LIGHT,
};

const containerArrayStyle = {
  display: "flex",
  flexDirection: "row",
  flexBasis: "0",
  flexWrap: "wrap",
  rowGap: "5dvh",
  justifyContent: "center",
  position: "absolute",
  top: "40%",
  transform: "translateY(-20%)",
  width: "100%",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  fontFamily: FONT,
  borderRadius: "0.25em",
  //backgroundColor: COLOR_DARK,
}

const inputStyle = {
  fontFamily: FONT,
  textTransform: "uppercase",
};

const buttonStyle = {
  padding: "10px 20px",
  fontSize: "3dvh",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  border: "none",
  cursor: "pointer",
  backgroundColor: COLOR_LIGHT,
  borderRadius: "0.25em",
  boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  color: "white",
  margin: "1.2dvh auto"
};

const headerStyle = {
  marginTop: "4dvh",
  //marginBottom: "4dvh",
  padding: "0 3dvh",
  fontFamily: FONT,
  fontSize: "4dvh",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
};

const messageStyle = {
  fontSize: "3dvh",
  fontWeight: "bold",
  fontFamily: FONT
};

/**
 * Returns HTML for a single container. Depending on isLast, sometimes returns plus-containers or equal-sign-containers and input field.
 * @param {String} romanChar The Roman number letter which will be displayed as a coin and as decimal.
 * @param {boolean} isLast Whether this is the last/rightmost container being rendered, in which case no plus will be added, and instead an equal sign and input field are added.
 */
function renderContainers(romanChar, isLast, input, setInput, checkAnswers, disableInput, buttonText) {
  let coinImage = "";
  switch (romanChar) {
    case 'M':
      coinImage=coin_M;
      break;
    case 'D':
      coinImage=coin_D;
      break;
    case 'C':
      coinImage=coin_C;
      break;
    case 'L':
      coinImage=coin_L;
      break;
    case 'X':
      coinImage=coin_X;
      break;
    case 'V':
      coinImage=coin_V;
      break;
    case 'I':
      coinImage=coin_I;
      break;
    default:
      console.log("Error renderContainers(): Invalid romanChar passed as argument!")
  }
  let coin = <span style={{width: "7.5dvh", height: "7.5dvh"}}><img src={coinImage} alt="coin" style={{ width: "100%", height: "100%", filter: "drop-shadow(0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5))",}}/></span>; // Use image instead of coin object.
  let decimal = convertToDecimal(ROMAN_SYSTEM, romanChar);
  return (
    <div style={{display: "flex", flexDirection: "row"}}>
      <div style={containerStyle}>
        {coin}
        <span style={{fontSize: "6dvh", fontFamily: ROMAN_FONT, fontWeight: "bold"}}>&#11107;</span>
        {decimal}
      </div>
      {isLast ? "" : 
        <div style={containerStyle}>
          <span style={{visibility: "hidden", width: "7dvh", height: "7dvh"}}>{coin}</span>
          <span style={{fontSize: "6dvh", fontFamily: ROMAN_FONT, fontWeight: "bold", visibility: "hidden"}}>&#11107;</span>
          <span style={{marginTop: "0.5dvh"}}>+</span>
        </div>}
      {!isLast ? "" : 
        <div style={containerStyle}>
          <span style={{visibility: "hidden", width: "7dvh", height: "7dvh"}}>{coin}</span>
          <span style={{fontSize: "6dvh", fontFamily: ROMAN_FONT, fontWeight: "bold", visibility: "hidden"}}>&#11107;</span>
          <span style={{marginTop: "0.5dvh"}}>=</span>
        </div>}
      {!isLast ? "" : 
        <div style={{display: "flex", flexDirection: "column", maxWidth: "20dvh", margin: "0 auto"}}>
          <span style={{visibility: "hidden", width: "7dvh", height: "7dvh"}}>{coin}</span>
          <span style={{fontSize: "6dvh", fontFamily: ROMAN_FONT, fontWeight: "bold", visibility: "hidden"}}>&#11107;</span>
          <span style={{marginTop: "0.1vh"}}>
            <input
            className="input-field"
            style={inputStyle}
            type="text"
            value={input}
            maxLength={5}
            disabled={disableInput}
            placeholder=""
            onChange={(e) => {
              //e.target.value = e.target.value.replace(/[^0-9]/g, ''); // LLM-generated. Makes sure only numeric input is allowed.
              setInput(e.target.value)}
            }
            />
          </span>
          
        </div>
      }
    </div>
  );
}

/**
 * Iterates through Roman string and renders all containers.
 * @param {number} number 
 * @param {String} input 
 * @param {*} setInput 
 * @param {*} checkAnswers 
 * @param {boolean} disableInput 
 * @returns 
 */
function iterateString(number, input, setInput, checkAnswers, disableInput, buttonText) {
  let romanString = convertToAlternate(ROMAN_SYSTEM, number);
  let containerArray = [];
  let isLast = false;
  for (let i = 0; i < romanString.length; ++i) {
    (i === (romanString.length - 1)) ? isLast = true : isLast = false; // Flag checking whether currently processing last Roman char.
    containerArray.push(renderContainers(romanString[i], isLast, input, setInput, checkAnswers, disableInput, buttonText)); // Create individual containers and put them in an array.
  }
  return <div style={containerArrayStyle}>{containerArray}</div>; // Render the array.
}

function Level0Roman({levelProgress, setLevelProgress}) {
  const [number, setNumber] = useState(
    generateNumber(levelProgress[LevelNumber])
  );
  const [input, setInput] = useState("");
  const [message, setMessage] = useState("");
  const [disableInput, setDisableInput] = useState(false);
  const [buttonText, setButtonText] = useState(buttonCheck);
  const [checkProgress, setCheckProgress] = useState(0);

  useEffect(() => {
    setNumber(generateNumber(levelProgress[LevelNumber]));
    setInput("");
  }, [levelProgress]);

  const checkAnswers = () => {
    const isCorrect = (parseInt(input) === number);
    if (checkProgress === 0) {
      if (isCorrect) {
        if (levelProgress[LevelNumber] + 1 === REPEATS) {
          setMessage(levelCorrect + nextLevelUnlocked);
        } else {
          setMessage(levelCorrect);
        }
      } else if (input === "") {
        setMessage(levelNoInput);
      } else {
        setMessage(levelIncorrect);
      }
      setDisableInput(true);
      setCheckProgress(1);
      setButtonText(buttonContinue);
    } else {
      if (isCorrect) {
        increaseProgress(levelProgress, setLevelProgress, LevelNumber, ROMAN_SYSTEM);
        setInput("");
      }
      setMessage("");
      setDisableInput(false);
      if (input !== "") {
        setInput("");
      }
      setCheckProgress(0);
      setButtonText(buttonCheck);
    }
  };
  return (
    <div>
      <div style={headerStyle}>
        {level0RomanTask1} <span style={{fontFamily: ROMAN_FONT, color: COLOR_HIGHLIGHTED, fontSize: "5dvh"}}>{convertToAlternate(ROMAN_SYSTEM, number)}</span> {level0RomanTask2}
      </div>
      <DndProvider options={HTML5toTouch}>
        <div>
          {iterateString(number, input, setInput, checkAnswers, disableInput, buttonText)}
        </div>
      </DndProvider>
      <button className="checkAnswer-button" onClick={checkAnswers}>
          {buttonText}
      </button>
      {message && <div className="checkAnswer-message">{message}</div>}
    </div>
  );
}

export default Level0Roman;
