import React, { useState, useEffect } from "react";
import "../App.css";
import {
  COLOR_DARK,
  COLOR_DIVISOR,
  COLOR_HIGHLIGHTED,
  COLOR_LIGHT,
  COLOR_MEDIUM,
  COLOR_VERYLIGHT,
  FONT,
  increaseProgress,
  MAYA_SYSTEM,
  REPEATS,
  ROMAN_FONT,
  ROMAN_SYSTEM,
} from "../defaults";
import {
  InputBoxCheck,
  levelCorrect,
  levelNoInput,
  level0RomanTask1,
  level0RomanTask2,
  levelIncorrect,
  nextLevelUnlocked,
  Tutorial0Continue,
  buttonContinue,
  buttonCheck,
  level0MayaTask,
} from "../strings";
import { convertToAlternate, convertToDecimal } from "../helpers";
import corn from "../images/corn.png";
import basket from "../images/basket.png";
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5toTouch } from "rdndmb-html5-to-touch";

const LevelNumber = 0;

function generateNumber(currentProgress) {
  const numbers = [2, 8, 14];
  if (currentProgress >= numbers.length) {
    return Math.round(Math.random() * 18) + 1; // Generate integer between 1 and 19.
  }
  return numbers[currentProgress];
}

const containerStyle = {
  display: "flex",
  flexDirection: "row",
  width: "100%",
  justifyContent: "center",
};

const containerArrayStyle = {
  display: "flex",
  flexDirection: "column",
  flexWrap: "wrap",
  rowGap: "3dvh",
  justifyContent: "center",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  fontFamily: FONT,
  borderRadius: "0.25em",
}

/* Partially LLM-generated. */
const taskStyle = {
  display: "flex", 
  justifyContent: "center", 
  alignItems: "center", 
  fontFamily: FONT, 
  fontSize: "5dvh",
  position: "absolute", // Fix the position of the container
  top: "50%", // Start the container at the center of the parent
  left: "50%", // Center it horizontally in the parent
  transform: "translate(-50%, -50%)", // Adjust the position to truly center it
}

const inputStyle = {
  fontFamily: FONT,
  fontSize: "5dvh", 
  maxWidth: "15dvh"
};

const headerStyle = {
  marginTop: "4dvh",
  padding: "0 3dvh",
  fontFamily: FONT,
  fontSize: "4dvh",
  textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
};

const mayaImages = [corn, basket];
const cornWidth = 10;

function renderImages(mayaArray) {
  let containerArray = [];
  if (mayaArray[0] > 0) { // Corn is rendered using a different style than the rest, because corn is rendered next to each other, while the rest is rendered above/below each other.
    containerArray.push(
      <div style={containerStyle}>
        {
          Array.from({length: mayaArray[0]}).map( () => { // Use array construct to render corn image exactly mayaArray[0] times.
            return <span style={{width: cornWidth+"dvh", height: cornWidth+"dvh"}}><img src={mayaImages[0]} alt="maya-image" style={{ width: "100%", height: "100%", filter: "drop-shadow(0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5))",}}/></span>
          })
        }
      </div>
    )
  }
  for (let i = 1; i < mayaArray.length; ++i) {
    while (mayaArray[i] > 0) {
      containerArray.push(
        <div style={containerStyle}>
          <span style={{width: (cornWidth*4)+"dvh", height: cornWidth+"dvh"}}><img src={mayaImages[i]} alt="maya-image" style={{ width: "100%", height: "100%", filter: "drop-shadow(0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5))",}}/></span>
        </div>
      ) 
      --mayaArray[i];
    }
  }

  return (
    <div style={containerArrayStyle}>
      {containerArray}
    </div>
  );
}

function Level0Maya({levelProgress, setLevelProgress}) {
  const [number, setNumber] = useState(
    generateNumber(levelProgress[LevelNumber])
  );
  const [input, setInput] = useState("");
  const [message, setMessage] = useState("");
  const [disableInput, setDisableInput] = useState(false);
  const [buttonText, setButtonText] = useState(buttonCheck);
  const [checkProgress, setCheckProgress] = useState(0);

  useEffect(() => {
    setNumber(generateNumber(levelProgress[LevelNumber]));
    setInput("");
  }, [levelProgress]);

  const checkAnswers = () => {
    const isCorrect = (parseInt(input) === number);
    if (checkProgress === 0) {
      if (isCorrect) {
        if (levelProgress[LevelNumber] + 1 === REPEATS) {
          setMessage(levelCorrect + nextLevelUnlocked);
        } else {
          setMessage(levelCorrect);
        }
      } else if (input === "") {
        setMessage(levelNoInput);
      } else {
        setMessage(levelIncorrect);
      }
      setDisableInput(true);
      setCheckProgress(1);
      setButtonText(buttonContinue);
    } else {
      if (isCorrect) {
        increaseProgress(levelProgress, setLevelProgress, LevelNumber, MAYA_SYSTEM);
      }
      setMessage("");
      setDisableInput(false);
      if (input !== "") {
        setInput("");
      }
      setCheckProgress(0);
      setButtonText(buttonCheck);
    }
  };
  return (
    <div>
      <div style={headerStyle}>
        {level0MayaTask}
      </div>
      <DndProvider options={HTML5toTouch}>
        <div style={taskStyle}>
          {renderImages(convertToAlternate(MAYA_SYSTEM, number))}
          <span style={{margin: "0 2dvh"}}>=</span>
          <input
            className="input-field"
            style={inputStyle}
            type="text"
            value={input}
            maxLength={5}
            disabled={disableInput}
            placeholder=""
            onChange={(e) => {
              e.target.value = e.target.value.replace(/[^0-9]/g, ''); // LLM-generated. Makes sure only numeric input is allowed.
              setInput(e.target.value)}
            }
          />
        </div>
      </DndProvider>
      <button className="checkAnswer-button" onClick={checkAnswers}>
          {buttonText}
      </button>
      {message && <div className="checkAnswer-message">{message}</div>}
    </div>
  );
}

export default Level0Maya;
