import React, { useState, useEffect } from "react";
import coin100 from "../images/coin100.svg";
import coin10 from "../images/coin10.svg";
import coin1 from "../images/coin1.svg";
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5toTouch } from "rdndmb-html5-to-touch";
import Box from "../components/Box";
import Coin from "../components/Coin";
import InputBox from "../components/InputBox";
import ProgressPopup from "../components/ProgressPopup";
import {
  COLOR_DIVISOR,
  COLOR_LIGHT,
  FONT,
  increaseProgress,
  REPEATS,
} from "../defaults";
import {
  levelCorrect,
  level2Welcome1,
  level2Welcome2,
  level7BiggerFirst,
  level7Exchange,
  level7Exchange1,
  level7ExchangeCoins,
  level7NotEnoughCoins,
  level7TooMany,
  level7Welcome,
  level7WrongDistributed,
  level7WrongNotDistributed,
  nextLevelUnlocked,
} from "../strings";

const LevelNumber = 8;

function calculateDividend(currentProgress) {
  const numbers = [233];
  if (currentProgress >= numbers.length) {
    return Math.floor(Math.random() * 900) + 100;
  }
  return numbers[currentProgress];
}

function calculateDivisor(currentProgress) {
  const numbers = [2];
  if (currentProgress >= numbers.length) {
    return Math.floor(Math.random() * 4) + 2;
  }
  return numbers[currentProgress];
}

const coinImages = {
  0: coin100,
  1: coin10,
  2: coin1,
};

function Level8({ levelProgress, setLevelProgress , whichSystem}) {
  const [dividend, setDividend] = useState(0);
  const [divisor, setDivisor] = useState(0);
  const [coins, setCoins] = useState(Array.from(String(dividend), Number));
  const [droppedCoins, setDroppedCoins] = useState([0, 0, 0]);
  const [coinsInBoxes, setCoinsInBoxes] = useState([0, 0, 0]);

  useEffect(() => {
    let divd = calculateDividend(levelProgress[LevelNumber]);
    let divr = calculateDivisor(levelProgress[LevelNumber]);
    let sum = Array.from(
      String(Math.floor(divd / divr)).padStart(3, "0"),
      Number
    ).reduce((partialSum, a) => partialSum + a, 0);
    while (
      Array.from(
        String(Math.floor(divd / divr)).padStart(3, "0"),
        Number
      ).reduce((max, a) => Math.max(max, a), 0) *
        divr >
        18 ||
      sum > 14
    ) {
      divd = calculateDividend(levelProgress[LevelNumber]);
      divr = calculateDivisor(levelProgress[LevelNumber]);
      sum = Array.from(
        String(Math.floor(divd / divr)).padStart(3, "0"),
        Number
      ).reduce((partialSum, a) => partialSum + a, 0);
    }
    setDividend(divd);
    setDivisor(divr);
    setCoins(Array.from(String(divd), Number));
    setDroppedCoins([0, 0, 0]);
    setCoinsInBoxes([0, 0, 0]);
  }, [levelProgress]);

  const handleDropCoin = (item) => {
    if (coins[item[0]] - droppedCoins[item[0]] < divisor && item[0] === 2) {
      setMessage(level7NotEnoughCoins);
      setShowModal(true);
    } else if (coins[item[0]] - droppedCoins[item[0]] < divisor) {
      setMessage(level7ExchangeCoins);
      setShowModal(true);
    } else {
      const newDroppedCoins = [...droppedCoins];
      newDroppedCoins[item[0]] += divisor;
      setDroppedCoins(newDroppedCoins);
      const newCoinsInBoxes = [...coinsInBoxes];
      newCoinsInBoxes[item[0]] += 1;
      setCoinsInBoxes(newCoinsInBoxes);
    }
  };

  function handleExchange(index) {
    if (index === 2) {
      setMessage(level7Exchange1);
      setShowModal(true);
    } else if (index === 1 && coins[0] !== droppedCoins[0]) {
      setMessage(level7BiggerFirst);
      setShowModal(true);
    } else if (
      coins[index] - droppedCoins[index] < divisor &&
      coins[index] - droppedCoins[index] > 0
    ) {
      const newCoins = [...coins];
      newCoins[index] = droppedCoins[index];
      newCoins[index + 1] += (coins[index] - droppedCoins[index]) * 10;
      setCoins(newCoins);
    } else {
      setMessage(level7TooMany);
      setShowModal(true);
    }
  }

  const [message, setMessage] = useState("");
  const [showModal, setShowModal] = useState(false);

  const handleCorrect = () => {
    if (levelProgress[LevelNumber] + 1 === REPEATS) {
      setMessage(levelCorrect + nextLevelUnlocked);
    } else {
      setMessage(levelCorrect);
    }
    setShowModal(true);
    increaseProgress(levelProgress, setLevelProgress, LevelNumber, whichSystem);
  };

  const handleIncorrect = () => {
    const result = Array.from(String(Math.floor(dividend / divisor)));
    let allBoxesFilled =
      coinsInBoxes[0] === parseInt(result[0]) &&
      coinsInBoxes[1] === parseInt(result[1]);
    allBoxesFilled = allBoxesFilled && coinsInBoxes[2] === parseInt(result[2]);
    if (allBoxesFilled) {
      setMessage(level7WrongDistributed);
    } else {
      setMessage(level7WrongNotDistributed);
    }
    setShowModal(true);
  };

  const hideModal = () => {
    setShowModal(false);
  };

  const exchangeStyle = {
    padding: "0.5dvh",
    margin: "0.5dvh",
    width: "10dvh",
    fontFamily: FONT,
    color: "white",
    fontSize: "2dvh",
    backgroundColor: COLOR_LIGHT,
    borderRadius: "0.25em",
    border: "none",
    boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  };

  const coinsStyle = {
    display: "flex  ",
    flexDirection: "column",
    flexWrap: "wrap",
    alignItems: "center",
    maxHeight: "50dvh",
  };

  return (
    <div>
      <div
        className="Welcome-label"
        style={{ fontFamily: FONT, fontSize: "3.5dvh" }}
      >
        {level2Welcome1}
        {dividend}
        {level2Welcome2}
        <span style={{ color: COLOR_DIVISOR }}>{divisor}</span>?<br></br>
        {level7Welcome}
      </div>
      <ProgressPopup show={showModal} handleClose={hideModal}>
        {message}
      </ProgressPopup>
      <DndProvider options={HTML5toTouch}>
        <div
          className="container"
          style={{ display: "flex", justifyContent: "space-around" }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              height: "50dvh",
              width: "30dvh",
            }}
          >
            {Array.from({ length: 3 }).map((_, id) => (
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <button
                  disabled={coins[id] === droppedCoins[id]}
                  onClick={() => handleExchange(id)}
                  style={{
                    ...exchangeStyle,
                    opacity: coins[id] === droppedCoins[id] ? 0 : 1,
                  }}
                >
                  {level7Exchange}
                </button>
                <div style={coinsStyle}>
                  {Array.from({ length: coins[id] - droppedCoins[id] }).map(
                    (_, idx) => (
                      <Coin
                        key={[id, idx]}
                        id={[id, idx]}
                        coinImage={coinImages[id]}
                      />
                    )
                  )}
                </div>
              </div>
            ))}
          </div>
          <div className="right-panel">
            {Array.from({ length: divisor }).map((_, idx) => (
              <Box id={idx} onDropCoin={handleDropCoin} coins={coinsInBoxes} />
            ))}
          </div>
          <div className="equation" style={{ padding: "0.5dvh" }}>
            <InputBox
              handleCorrect={handleCorrect}
              handleIncorrect={handleIncorrect}
              dividend={dividend}
              divisor={divisor}
              remainder={dividend % divisor}
            ></InputBox>
          </div>
        </div>
      </DndProvider>
    </div>
  );
}

export default Level8;
