/**
 * Converts input decimal to Roman string.
 * @param {number} number 
 */
export function decToRoman(number) {
    if (number <= 0) {
        console.log("decToRoman: number must be positive!"); // TODO: Print message to screen.
        return "";
    }
    let outputString = "";
    while (number > 0) {
        if (number >= 1000) {
            outputString += "M";
            number -= 1000;
            continue;
        }
        if (number >= 500) {
            outputString += "D";
            number -= 500;
            continue;
        }
        if (number >= 100) {
            outputString += "C";
            number -= 100;
            continue;
        }
        if (number >= 50) {
            outputString += "L";
            number -= 50;
            continue;
        }
        if (number >= 10) {
            outputString += "X";
            number -= 10;
            continue;
        }
        if (number >= 5) {
            outputString += "V";
            number -= 5;
            continue;
        }
        if (number >= 1) {
            outputString += "I";
            number -= 1;
            continue;
        }
    }
    return outputString;
}

export function decToBinary(number) {
    if (number <= 0) {
        console.log("decToBinary: number must be positive!"); // TODO: Print message to screen.
        return;
    }
    // Note that if code reaches here, number is strictly positive.
    let outputString = Number(number).toString(2);
    return outputString;
}

export function decToMaya(number) {
    if (number <= 0) {
        console.log("decToMaya: number must be positive!"); // TODO: Print message to screen.
        return;
    }
    // Note that if code reaches here, number is strictly positive.
    let outputArray = [0,0,0];
    while (number > 0) {
        if (number >= 20) {
            ++outputArray[2]; // Add a cart.
            number -= 20;
            continue;
        }
        if (number >= 5) {
            ++outputArray[1]; // Add a basket.
            number -= 5;
            continue;
        }
        if (number >= 1) {
            ++outputArray[0]; // Add corn.
            number -= 1;
            continue;
        }
    }
    return outputArray;
}

/**
 * LLM-generated. Calculates character frequencies for all Roman characters in a string.
 * @param {*} str 
 * @returns Object with key-value pairs. Example output for 'hello world': { h: 1, e: 1, l: 3, o: 2, ' ': 1, w: 1, r: 1, d: 1, '!': 1 }
 */
export function countRomanCharOccurrences(str) {
    const romanChars = "IVXLCDM";
    return Array.from(str).reduce((acc, char) => {
      if (romanChars.includes(char)) {
        acc[char] = (acc[char] || 0) + 1;
      }
      return acc;
    }, Object.fromEntries(romanChars.split("").map((char) => [char, 0]))); // For romanChars, outputs them with a value of 0 even if they don't occur. If other chars don't occur, they are not output at all.
  }
  