import React, { useState, useImperativeHandle, forwardRef } from "react";
import {
  COLOR_DARK,
  COLOR_DIVISOR,
  COLOR_LIGHT,
  COLOR_MEDIUM,
  FONT,
  ROMAN_FONT,
  ROMAN_SYSTEM,
  BINARY_FONT,
  BINARY_SYSTEM
} from "../defaults";
import { InputBoxResult, InputBoxRemainder, InputBoxCheck } from "../strings";
import { convertToAlternate, convertToDecimal } from "../helpers";
/*
This component implements a input box into which the divisor,
and optionally the remainder can be entered.
The check button only appears when the fields have been filled.
If the input is wrong, it gets reset and handleIncorrect gets called.
If it is correct, handleCorrect is called.
*/
const InputBox = forwardRef((
  {handleCorrect,
  handleIncorrect,
  handleNoInput,
  dividend,
  divisor,
  remainder,
  whichSystem},
  ref
) => {
  const [inputQuotient, setInputQuotient] = useState("");
  const [inputRemainder, setInputRemainder] = useState("");

  // These are the same ones as above, just converted to the altenate numeral system. They are updated and checked everytime the decimal ones are.
  const [inputQuotientAlternate, setInputQuotientAlternate] = useState("");
  const [inputRemainderAlternate, setInputRemainderAlternate] = useState("");

  const handleQuotientChange = (e) => {
    e.target.value = e.target.value.replace(/[^0-9]/g, ''); // LLM-generated. Makes sure only numeric input is allowed.
    setInputQuotient(e.target.value);
  };

  const handleQuotientAlternateChange = (e) => {
    whichSystem === ROMAN_SYSTEM ? e.target.value = e.target.value.replace(/[^a-zA-Z]/g, '') : e.target.value = e.target.value.replace(/[^0-1]/g, ''); // LLM-generated. If Roman system, makes sure only letter-input is allowed. Note that this does not allow mutated vowels like ä,ö etc. For binary, allows only 0s or 1s.
    setInputQuotientAlternate(e.target.value);
   };

  const handleRemainderChange = (e) => {
    e.target.value = e.target.value.replace(/[^0-9]/g, ''); // LLM-generated. Makes sure only numeric input is allowed.
    setInputRemainder(e.target.value);
  };

  const handleRemainderAlternateChange = (e) => {
    whichSystem === ROMAN_SYSTEM ? e.target.value = e.target.value.replace(/[^a-zA-Z]/g, '') : e.target.value = e.target.value.replace(/[^0-1]/g, ''); // LLM-generated. If Roman system, makes sure only letter-input is allowed. Note that this does not allow mutated vowels like ä,ö etc. For binary, allows only 0s or 1s.
    setInputRemainderAlternate(e.target.value);
  };

  // Need to differentiate between remainder and no remainder because Roman numbers don't have a sign for the number 0.
  const checkInput = () => {
    if (remainder === 0) { // No remainder.
      if (inputQuotient === "" || inputQuotientAlternate === "") { // Not all fields filled.
        handleNoInput();
      }
      else if ( // Input was correct.
      parseInt(inputQuotient) === dividend / divisor && parseInt(convertToDecimal(whichSystem,inputQuotientAlternate)) === dividend / divisor) {
        handleCorrect();
        setInputQuotient("");
        setInputQuotientAlternate("");
      }
      else { // Input was wrong. All input fields with wrong inputs are reset.
        handleIncorrect();
        if (parseInt(inputQuotient) !== dividend / divisor) {
          setInputQuotient("");
        }
        if (parseInt(convertToDecimal(whichSystem,inputQuotientAlternate)) !== dividend / divisor) {
          setInputQuotientAlternate("");
        }
        if (whichSystem === BINARY_SYSTEM && inputQuotientAlternate.startsWith("0")) { // If binary, we don't allow leading 0s.
          handleIncorrect(true); // This handleIncorrect call with argument "true" overrides the above call without argument.
          setInputQuotientAlternate("");
        }
      }
    } 
    else { // Some remainder.
      if (inputQuotient === "" || inputQuotientAlternate === "" || inputRemainder === "" || inputRemainderAlternate === "") { // Not all fields filled.
        handleNoInput();
      }
      else if ( // Input was correct.
      parseInt(inputQuotient) === (dividend - remainder) / divisor && parseInt(inputRemainder) === remainder && parseInt(convertToDecimal(whichSystem,inputQuotientAlternate)) === (dividend - remainder) / divisor && parseInt(convertToDecimal(whichSystem, inputRemainderAlternate)) === remainder) {
        handleCorrect();
        setInputQuotient("");
        setInputRemainder("");
        setInputQuotientAlternate("");
        setInputRemainderAlternate("");
      } 
      else { // Input was wrong. All input fields with wrong inputs are reset.
        handleIncorrect();
        if (parseInt(inputQuotient) !== (dividend - remainder) / divisor) {
          setInputQuotient("");
        }
        if (parseInt(inputRemainder) !== remainder) {
          setInputRemainder("");
        }
        if (parseInt(convertToDecimal(whichSystem,inputQuotientAlternate)) !== (dividend - remainder) / divisor) {
          setInputQuotientAlternate("");
        }
        if (parseInt(convertToDecimal(whichSystem, inputRemainderAlternate)) !== remainder) {
          setInputRemainderAlternate("");
        }
        if (whichSystem === BINARY_SYSTEM && inputQuotientAlternate.startsWith("0")) { // If binary, we don't allow leading 0s.
          handleIncorrect(true); // This handleIncorrect call with argument "true" overrides the above call without argument.
          setInputQuotientAlternate("");
        }
        if (whichSystem === BINARY_SYSTEM && inputRemainderAlternate.startsWith("0")) { // If binary, we don't allow leading 0s.
          handleIncorrect(true); // This handleIncorrect call with argument "true" overrides the above call without argument.
          setInputRemainderAlternate("");
        }
      }
    }
  };

  useImperativeHandle(ref, () => ({
    checkInput,
  }));

  const quotientLength = convertToAlternate(whichSystem,parseInt(dividend - remainder / divisor)).length; // Number of chars in solution. Used to scale width of input field.

  const inputStyle = {
    fontFamily: FONT,
    boxShadow: "0.9svh 0.9svh 0.6svh rgba(0, 0.5, 0, 0.5)",
  };

  const inputStyleAlternate = {
    fontFamily: (whichSystem === ROMAN_SYSTEM) ? ROMAN_FONT : BINARY_FONT, // Works because this input field is only used for Roman and binary, not for Maya.
    textTransform: "uppercase",
    boxShadow: "0.7svh 0.7svh 0.6svh rgba(0, 0.5, 0, 0.5)",
  };

  return (
    <div>
      <div
        style={{
          display: "grid",
          gridTemplateRows: "1fr 1fr",
          gridTemplateColumns: (remainder > 0) ? "repeat(6, max-content)" : "repeat(5, max-content)", // Widths of the columns. If there is a remainder, add an additional column.
          alignItems: "center",
          justifyContent: "center",
          rowGap: "2dvh",
          fontFamily: FONT,
          textShadow:"0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
          //maxWidth: "40dvh",
          backgroundColor: COLOR_DARK,
          padding: "2dvh",
          borderRadius: "0.25em",
          boxShadow: "0.3dvh 0.3dvh 0.6dvh rgba(0, 0, 0, 0.5)"
        }}
      >

        <div style={{textAlign: "right", fontFamily: (whichSystem === ROMAN_SYSTEM) ? ROMAN_FONT : BINARY_FONT}}>{convertToAlternate(whichSystem, dividend)}</div>
        <div>&nbsp;&nbsp;:&nbsp;&nbsp;</div>
        <div><span style={{ color: COLOR_DIVISOR }}>{divisor}</span></div>
        <div>&nbsp;&nbsp;=&nbsp;&nbsp;</div>
        <div><input type="text"
          value={inputQuotientAlternate}
          placeholder=""
          onChange={handleQuotientAlternateChange}
          className="input-field"
          style={{...inputStyleAlternate, maxWidth: 2.1*quotientLength+"dvh", minWidth: "10dvh"}}
        /></div>
        {remainder > 0 && (
        <div>
          &nbsp;&nbsp;&nbsp;{InputBoxRemainder}&nbsp;&nbsp;&nbsp;
          <input
            type="text"
            value={inputRemainderAlternate}
            placeholder=""
            onChange={handleRemainderAlternateChange}
            className="input-field"
            style={inputStyleAlternate}
          />
        </div>
        )}

        <div style={{textAlign: "right"}}>{dividend}</div>
        <div>&nbsp;&nbsp;:&nbsp;&nbsp;</div>
        <div><span style={{ color: COLOR_DIVISOR }}>{divisor}</span></div>
        <div>&nbsp;&nbsp;=&nbsp;&nbsp;</div>
        <div><input type="text"
          value={inputQuotient}
          placeholder=""
          onChange={handleQuotientChange}
          className="input-field"
          style={{...inputStyle, maxWidth: 2.1*quotientLength+"dvh", minWidth: "10dvh"}}
        /></div>
        {remainder > 0 && (
        <div>
          &nbsp;&nbsp;&nbsp;{InputBoxRemainder}&nbsp;&nbsp;&nbsp;
          <input
            type="text"
            value={inputRemainder}
            placeholder=""
            onChange={handleRemainderChange}
            className="input-field"
            style={inputStyle}
          />
        </div>
        )}
      </div>
  </div>
  );
});

export default InputBox;
