import React, { useState, useEffect } from "react";
import "../App.css";
import {
  COLOR_DARK,
  COLOR_DIVISOR,
  COLOR_HIGHLIGHTED,
  COLOR_LIGHT,
  COLOR_MEDIUM,
  FONT,
  increaseProgress,
  REPEATS,
  ROMAN_FONT,
  ROMAN_SYSTEM,
} from "../defaults";
import {
    buttonCheck,
    buttonContinue,
  InputBoxCheck,
  InputBoxRemainder,
  levelCorrect,
  level2Welcome1,
  level2Welcome2,
  levelIncorrect,
  level5EqualResults,
  level5Numbers,
  level5Welcome,
  level6EqualRemainders,
  level6RemainderSmaller1,
  level6RemainderSmaller2,
  level6RemainderSmaller3,
  levelNoInputMulti,
  nextLevelUnlocked,
  Tutorial0Continue,
} from "../strings";
import { convertToAlternate, convertToDecimal } from "../helpers";

const LevelNumber = 4;

// The ranges of multiplicands, multipliers and remainders produce a range for the quotient of 5-19.
function calculateMultiplicands(currentProgress) {
  const numbers = [2, 4, 3];
  if (currentProgress >= numbers.length) {
    return Math.round(Math.random() * 2) + 2; // Range 2-4
  }
  return numbers[currentProgress];
}

function calculateMultipliers(currentProgress) {
  const numbers = [4, 4, 2];
  if (currentProgress >= numbers.length) {
    return Math.round(Math.random() * 2) + 2; // Range 2-4
  }
  return numbers[currentProgress];
}

// Make sure there is always a remainder, i.e. it is never zero.
function calculateRemainders(multiplicand, currentProgress) {
  const numbers = [1, 3, 1];
  if (currentProgress >= numbers.length) {
    return Math.round(Math.random() * (multiplicand-2)) + 1; // Range 1-3 (1-multiplicand)
  }
  return numbers[currentProgress];
}


function Level4Roman({levelProgress, setLevelProgress}) {
  const [multiplicand, setMultiplicand] = useState(
    calculateMultiplicands(levelProgress[LevelNumber])
  );
  const [multiplier, setMultiplier] = useState(
    calculateMultipliers(levelProgress[LevelNumber])
  );
  const [remainder, setRemainder] = useState(
    calculateRemainders(multiplicand, levelProgress[LevelNumber])
  );
  const [message, setMessage] = useState("");
  const [disableInput, setDisableInput] = useState(false);
  const [buttonText, setButtonText] = useState(buttonCheck);
  const [checkProgress, setCheckProgress] = useState(0);

  useEffect(() => {
    const mult = calculateMultiplicands(levelProgress[LevelNumber]);
    setMultiplicand(mult);
    setMultiplier(calculateMultipliers(levelProgress[LevelNumber]));
    setRemainder(calculateRemainders(mult, levelProgress[LevelNumber]));
    setInputMultiplier("");
    setInputQuotient("");
    setInputRemainder1("");
    setInputRemainder2("");
  }, [levelProgress]);

  const [inputMultiplier, setInputMultiplier] = useState("");
  const [inputQuotient, setInputQuotient] = useState("");
  const [inputRemainder1, setInputRemainder1] = useState("");
  const [inputRemainder2, setInputRemainder2] = useState("");
  //const [highlightedInput, setHighlightedInput] = useState(0);

  const checkAnswers = () => {
    const isMultiplierCorrect = convertToDecimal(ROMAN_SYSTEM, inputMultiplier) === multiplier;
    const isQuotientCorrect = convertToDecimal(ROMAN_SYSTEM, inputQuotient) === multiplier;
    const isRemainder1Correct = convertToDecimal(ROMAN_SYSTEM, inputRemainder1) === remainder;
    const isRemainder2Correct = convertToDecimal(ROMAN_SYSTEM, inputRemainder2) === remainder;
    if (checkProgress === 0) {
      if (
        isMultiplierCorrect &&
        isQuotientCorrect &&
        isRemainder1Correct &&
        isRemainder2Correct
      ) {
        if (levelProgress[LevelNumber] + 1 === REPEATS) {
          setMessage(levelCorrect + nextLevelUnlocked);
        } else {
          setMessage(levelCorrect);
        }
      } else if (
        inputMultiplier === "" ||
        inputQuotient === "" ||
        inputRemainder1 === "" ||
        inputRemainder2 === ""
      ) {
        setMessage(levelNoInputMulti);
      } else if (inputMultiplier !== inputQuotient) {
        setMessage(level5EqualResults);
        //setHighlightedInput(1);
      } else if (inputRemainder1 !== inputRemainder2) {
        setMessage(level6EqualRemainders);
        //setHighlightedInput(2);
      } else if (parseInt(convertToDecimal(ROMAN_SYSTEM, inputRemainder1)) >= multiplicand) {
        setMessage(
          <div>
            {level6RemainderSmaller1}
            <span style={{ color: COLOR_DIVISOR }}>
              {" "}
              {level6RemainderSmaller2}{" "}
            </span>{" "}
            {level6RemainderSmaller3}{" "}
          </div>
        );
        //setHighlightedInput(2);
        setInputRemainder1("");
        setInputRemainder2("");
      } else if (isRemainder1Correct) {
        setMessage(levelIncorrect);
        //setHighlightedInput(1);
        setInputMultiplier("");
        setInputQuotient("");
      } else if (isMultiplierCorrect) {
        setMessage(levelIncorrect);
        //setHighlightedInput(2);
        setInputRemainder1("");
        setInputRemainder2("");
      } else {
        setMessage(levelIncorrect);
        setInputMultiplier("");
        setInputQuotient("");
        setInputRemainder1("");
        setInputRemainder2("");
      }
      setDisableInput(true);
      setCheckProgress(1);
      setButtonText(buttonContinue);
    } else {
      if (
        isMultiplierCorrect &&
        isQuotientCorrect &&
        isRemainder1Correct &&
        isRemainder2Correct
      ) {
        increaseProgress(levelProgress, setLevelProgress, LevelNumber, ROMAN_SYSTEM);
        setInputMultiplier("");
        setInputQuotient("");
        setInputRemainder1("");
        setInputRemainder2("");
      }
      setDisableInput(false);
      //setHighlightedInput(0);
      setMessage("");
      setCheckProgress(0);
      setButtonText(buttonCheck);
    }
  };

  const inputStyle = {
    color: "white",
    fontSize: "6dvh",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    backgroundColor: "white",
    borderRadius: "0.25em",
    //boxShadow: "inset 0.3svh 0.3svh 0.6svh rgba(0, 0.5, 0, 0.5)",
    textAlign: "center",
    maxWidth: "25dvh",
    fontFamily: ROMAN_FONT,
    color: COLOR_DARK,
    textTransform: "uppercase",
    boxShadow: "0.9svh 0.9svh 0.6svh rgba(0, 0.5, 0, 0.5)",
  };

  // Height + 2*top must be 100% so that it is centered nicely.
  const containerWrapperStyle = {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    position: "absolute",
    top: "20%",
    //transform: "translateY(-40%)",
    width: "100%",
    height: "60%",
    //backgroundColor: "black",
  }

  const containerStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    fontFamily: FONT,
    padding: "10dvh",
    borderRadius: "0.5em",
    boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    maxWidth: "80%",
    //maxHeight: "60%",
    margin: "0 auto",
    justifyContent: "center",
    backgroundColor: COLOR_DARK,
  };

  const rowStyle = {
    display: "flex",
    alignItems: "center",
    fontSize: "6dvh",
  };

  const buttonStyle = {
    padding: "10px 20px",
    fontSize: "3dvh",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    border: "none",
    cursor: "pointer",
    backgroundColor: COLOR_LIGHT,
    borderRadius: "0.25em",
    boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    color: "white",
  };

  const messageStyle = {
    marginTop: "10px",
    fontSize: "3dvh",
    fontWeight: "bold",
  };

  return (
    <div>
      <div
        className="Welcome-label"
        style={{ fontFamily: FONT, fontSize: "4dvh" }}
      >
        {level2Welcome1}
        <span style={{fontFamily: ROMAN_FONT, color: COLOR_HIGHLIGHTED, fontSize: "5dvh"}}>{convertToAlternate(ROMAN_SYSTEM, multiplicand * multiplier + remainder)}</span>
        {level2Welcome2}
        <span style={{ color: COLOR_DIVISOR }}>{multiplicand}</span>?<br></br>
      </div>
      <div style={containerWrapperStyle}>
        <div style={containerStyle}>
          <style>
            {`
              /* Hide arrows in input of type number for WebKit browsers (Chrome, Safari) This was generated by a LLM.*/
              input[type="number"]::-webkit-inner-spin-button,
              input[type="number"]::-webkit-outer-spin-button {
                  -webkit-appearance: none;
                  margin: 0;
              }

              /* Hide arrows in input of type number for Firefox */
              input[type="number"] {
                  -moz-appearance: textfield;
              }
            `}
          </style>
          <div style={{...rowStyle, marginBottom: "5dvh"}}>
            <span style={{fontFamily: ROMAN_FONT}}>{convertToAlternate(ROMAN_SYSTEM, multiplicand * multiplier + remainder)}</span>&nbsp;&nbsp;:&nbsp;&nbsp;
            <span style={{ color: COLOR_DIVISOR }}>{multiplicand}</span>
            &nbsp;&nbsp;=&nbsp;&nbsp;
            <input
              className="input-field"
              type="text"
              disabled={disableInput}
              value={inputQuotient}
              onChange={(e) => setInputQuotient(e.target.value)}
              maxLength={5}
              style={{
                ...inputStyle,
                /* backgroundColor:
                  highlightedInput === 1 ? COLOR_HIGHLIGHTED : inputStyle.backgroundColor , */
              }}
            />
            &nbsp;&nbsp;{InputBoxRemainder}&nbsp;&nbsp;
            <input
              className="input-field"
              type="text"
              disabled={disableInput}
              value={inputRemainder2}
              onChange={(e) => setInputRemainder2(e.target.value)}
              maxLength={5}
              style={{
                ...inputStyle,
                /* backgroundColor:
                  highlightedInput === 2 ? COLOR_HIGHLIGHTED : inputStyle.backgroundColor, */
              }}
            />
          </div>

          <div style={rowStyle}>
            <span style={{ color: COLOR_DIVISOR }}>{multiplicand}</span>
            &nbsp;&nbsp;x&nbsp;&nbsp;
            <input
              className="input-field"
              type="text"
              disabled={disableInput}
              value={inputMultiplier}
              onChange={(e) => setInputMultiplier(e.target.value)}
              maxLength={5}
              style={{
                ...inputStyle,
                /* backgroundColor:
                  highlightedInput === 1 ? COLOR_HIGHLIGHTED : inputStyle.backgroundColor, */
              }}
            />
            &nbsp;&nbsp;+&nbsp;&nbsp;
            <input
              className="input-field"
              type="text"
              disabled={disableInput}
              value={inputRemainder1}
              onChange={(e) => setInputRemainder1(e.target.value)}
              maxLength={5}
              style={{
                ...inputStyle,
                /* backgroundColor:
                  highlightedInput === 2 ? COLOR_HIGHLIGHTED : inputStyle.backgroundColor, */
              }}
            />
            &nbsp;&nbsp;=&nbsp;&nbsp;<span style={{fontFamily: ROMAN_FONT}}>{convertToAlternate(ROMAN_SYSTEM, multiplicand * multiplier + remainder)}</span>
          </div>
        </div>
      </div>
      
      <button className="checkAnswer-button" onClick={checkAnswers}>
        {buttonText}
      </button>
      {message && <div className="checkAnswer-message">{message}</div>}
      
    </div>
  );
}

export default Level4Roman;
