import React, { useState, useEffect, useCallback } from "react";
import { BINARY_SYSTEM, MAYA_SYSTEM, NUMBER_OF_LEVELS, REPEATS, ROMAN_SYSTEM, ROMAN_NUMBER_OF_LEVELS, BINARY_NUMBER_OF_LEVELS, MAYA_NUMBER_OF_LEVELS} from "./defaults";
import Tutorial0 from "./tutorials/tutorial0";
import Level0Roman from "./levels/level0Roman";
import Level1Roman from "./levels/level1Roman";
import Level2Roman from "./levels/level2Roman";
import Level3Roman from "./levels/level3Roman";
import Level4Roman from "./levels/level4Roman";
import Level5Roman from "./levels/level5Roman";
import Level0Binary from "./levels/level0Binary";
import Level1Binary from "./levels/level1Binary";
import Level2Binary from "./levels/level2Binary";
import Level3Binary from "./levels/level3Binary";
import Level4Binary from "./levels/level4Binary";
import Level5Binary from "./levels/level5Binary";
import Level6Binary from "./levels/level6Binary";
import Level7Binary from "./levels/level7Binary";
import Level0Maya from "./levels/level0Maya";
import Level1Maya from "./levels/level1Maya";
import Level0 from "./levels/level0";
import Level1 from "./levels/level1";
import Level2 from "./levels/level2";
import Level3 from "./levels/level3";
import Level4 from "./levels/level4";
import Level5 from "./levels/level5";
import Level6 from "./levels/level6";
import Level7 from "./levels/level7";
import Level8 from "./levels/level8";
import Level9 from "./levels/level9";
import Level10 from "./levels/level10";
import Level11 from "./levels/level11";
import Level12 from "./levels/level12";
import Tutorial1 from "./tutorials/tutorial1";
import Tutorial2 from "./tutorials/tutorial2";
import Tutorial3 from "./tutorials/tutorial3";
import Tutorial4 from "./tutorials/tutorial4";
import Tutorial5 from "./tutorials/tutorial5";
import Tutorial6 from "./tutorials/tutorial6";
import Tutorial7 from "./tutorials/tutorial7";
import Tutorial8 from "./tutorials/tutorial8";
import Tutorial9 from "./tutorials/tutorial9";
import Tutorial10 from "./tutorials/tutorial10";
import Tutorial11 from "./tutorials/tutorial11";
import Tutorial12 from "./tutorials/tutorial12";

function Level({
  level,
  setLevel,
  progress,
  setProgress,
  levelProgress,
  setLevelProgress,
  tutorialProgress,
  setTutorialProgress,
  whichSystem,
  numberOfLevels
}) {
  const [renderedComponent, setRenderedComponent] = useState(null);
  
  /*const increaseLevel = useCallback(() => {
    if (level === progress && progress+1 < NUMBER_OF_LEVELS) {
      setProgress(progress + 1);
    }

  }, [level, progress, setProgress]);*/

  const unlockAll = (() => {
    setProgress(ROMAN_SYSTEM,ROMAN_NUMBER_OF_LEVELS-1);
    setProgress(BINARY_SYSTEM,BINARY_NUMBER_OF_LEVELS-1);
    setProgress(MAYA_SYSTEM,MAYA_NUMBER_OF_LEVELS-1);
  });

  /*const reset = (() => {
    setProgress(ROMAN_SYSTEM, 0);
    setProgress(BINARY_SYSTEM, 0);
    setProgress(MAYA_SYSTEM, 0);
    setLevel(0);
    const newProgress = [...levelProgress(whichSystem)]
    const newTutorialProgress = [...tutorialProgress]
    for(let i=0; i<numberOfLevels(whichSystem); i++){
      newProgress[i] = 0;
      newTutorialProgress[i] = 0;
    }
    setLevelProgress(newProgress);
    setTutorialProgress(newTutorialProgress);
  })*/
 
  /**
   * Copy of reset function in App.jsx. This one is purely for debugging purposes. Delete before production.
   */
  const reset = (() => {
        setProgress(ROMAN_SYSTEM, 0);
        setProgress(BINARY_SYSTEM, 0);
        setProgress(MAYA_SYSTEM, 0);
        setLevel(0);
        const newRomanProgress = [...levelProgress(ROMAN_SYSTEM)] // Copy structure.
        const newBinaryProgress = [...levelProgress(BINARY_SYSTEM)]
        const newMayaProgress = [...levelProgress(MAYA_SYSTEM)]
        const newTutorialProgress = [...tutorialProgress]
        // Explicitly deleting and creating a new array for each system, because otherwise they might point to the same array.
        for(let i=0; i<numberOfLevels(ROMAN_SYSTEM); i++){
          newRomanProgress[i] = 0;
          //newTutorialProgress[i] = 0;
        }
        for(let i=0; i<numberOfLevels(BINARY_SYSTEM); i++){
          newBinaryProgress[i] = 0;
          //newTutorialProgress[i] = 0;
        }
        for(let i=0; i<numberOfLevels(MAYA_SYSTEM); i++){
          newMayaProgress[i] = 0;
          //newTutorialProgress[i] = 0;
        }
        setLevelProgress(ROMAN_SYSTEM,newRomanProgress);
        setLevelProgress(BINARY_SYSTEM,newBinaryProgress);
        setLevelProgress(MAYA_SYSTEM,newMayaProgress);
        setTutorialProgress(newTutorialProgress);
      })

  useEffect(() => {
    if (
      levelProgress(whichSystem)[level] >= REPEATS &&
      level === progress(whichSystem) &&
      progress(whichSystem) + 1 < numberOfLevels(whichSystem)
    ) {
      setProgress(whichSystem, progress(whichSystem) + 1);
    }
  }, [levelProgress, level, progress, setProgress]); // TODO: do i need to change progress to progress(whichSystem)?

  useEffect(() => {
    const renderLevelComponent = () => {
      const tutorialDone = (tutorialLevel) => {
        const newProgress = [...tutorialProgress];
        newProgress[tutorialLevel] = 1;
        setTutorialProgress(newProgress);
      };
      const levelProgressFixed = levelProgress(whichSystem);
      switch (whichSystem) {
        case ROMAN_SYSTEM:
          switch (level) {
            case 0:
              return tutorialProgress[level] === 0 ? (
                <Tutorial0 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level0Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 1:
              return tutorialProgress[level] === 0 ? (
                <Tutorial1 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level1Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 2:
              return tutorialProgress[level] === 0 ? (
                <Tutorial2 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level2Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 3:
              return tutorialProgress[level] === 0 ? (
                <Tutorial3 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level3Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 4:
              return tutorialProgress[level] === 0 ? (
                <Tutorial4 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level4Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 5:
              return tutorialProgress[level] === 0 ? (
                <Tutorial5 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level5Roman
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            default:
              return <div>Level {level} not implemented yet</div>;
          }
        case BINARY_SYSTEM:
          switch (level) {
            case 0:
              return tutorialProgress[level] === 0 ? (
                <Tutorial0 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level0Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 1:
              return tutorialProgress[level] === 0 ? (
                <Tutorial1 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level1Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 2:
              return tutorialProgress[level] === 0 ? (
                <Tutorial2 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level2Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 3:
              return tutorialProgress[level] === 0 ? (
                <Tutorial3 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level3Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 4:
              return tutorialProgress[level] === 0 ? (
                <Tutorial4 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level4Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 5:
              return tutorialProgress[level] === 0 ? (
                <Tutorial5 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level5Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 6:
              return tutorialProgress[level] === 0 ? (
                <Tutorial6 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level6Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 7:
              return tutorialProgress[level] === 0 ? (
                <Tutorial7 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level7Binary
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            default:
              return <div>Level {level} not implemented yet</div>;
          }
        case MAYA_SYSTEM:
          switch (level) {
            case 0:
              return tutorialProgress[level] === 0 ? (
                <Tutorial0 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level0Maya
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 1:
              return tutorialProgress[level] === 0 ? (
                <Tutorial1 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level1Maya
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                />
              );
            case 2:
              return tutorialProgress[level] === 0 ? (
                <Tutorial2 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level2
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 3:
              return tutorialProgress[level] === 0 ? (
                <Tutorial3 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level3
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 4:
              return tutorialProgress[level] === 0 ? (
                <Tutorial4 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level4
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 5:
              return tutorialProgress[level] === 0 ? (
                <Tutorial5 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level5
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem = {whichSystem}
                />
              );
            case 6:
              return tutorialProgress[level] === 0 ? (
                <Tutorial6 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level6
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 7:
              return tutorialProgress[level] === 0 ? (
                <Tutorial7 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level7
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 8:
              return tutorialProgress[level] === 0 ? (
                <Tutorial8 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level8
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 9:
              return tutorialProgress[level] === 0 ? (
                <Tutorial9 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level9
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 10:
              return tutorialProgress[level] === 0 ? (
                <Tutorial10 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level10
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 11:
              return tutorialProgress[level] === 0 ? (
                <Tutorial11 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level11
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            case 12:
              return tutorialProgress[level] === 0 ? (
                <Tutorial12 finishTutorial={() => tutorialDone(level)} />
              ) : (
                <Level12
                  levelProgress={levelProgressFixed}
                  setLevelProgress={setLevelProgress}
                  whichSystem={whichSystem} // needed for testing, but will not be passed to production levels.
                />
              );
            default:
              return <div>Level {level} not implemented yet</div>;
          }
      }




      
    };
    setRenderedComponent(renderLevelComponent());
  }, [
    level,
    levelProgress,
    setLevelProgress,
    progress,
    tutorialProgress,
    setTutorialProgress,
    whichSystem
  ]);

  return (
    <div>
          {/*<button onClick={increaseLevel}>DEBUG: Unlock next</button>*/}
          {/*
          <button onClick={unlockAll}>DEBUG: Unlock all</button>
          <button onClick={reset}>DEBUG: Reset</button>*/}
      {renderedComponent}
    </div>
  );
}

export default Level;
