import React, { useState, useEffect, useRef } from "react";
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5toTouch } from "rdndmb-html5-to-touch";
import BinaryBoxDestination from "../components/BinaryBoxDestination";
import BinaryBoxOrigin from "../components/BinaryBoxOrigin";
import {
  COLOR_DIVISOR,
  COLOR_LIGHT,
  COLOR_HIGHLIGHTED,
  FONT,
  increaseProgress,
  REPEATS,
  ROMAN_SYSTEM,
  ROMAN_FONT,
  COLOR_DARK,
  BINARY_SYSTEM,
  BINARY_FONT
} from "../defaults";
import {
  levelCorrect,
  levelIncorrect,
  level2RomanDropLargerFirst,
  level2Welcome1,
  level2Welcome2,
  level7BiggerFirst,
  level7Exchange,
  level7Exchange1,
  level7ExchangeCoins,
  level7NotEnoughCoins,
  level7TooMany,
  level7Welcome,
  level7WrongBox,
  level7WrongDistributed,
  level7WrongNotDistributed,
  levelNoInputMulti,
  nextLevelUnlocked,
  buttonCheck,
  buttonContinue,
  level1BinaryNoneDropped,
  level1BinaryTask1,
  level1BinaryTask2,
  level1BinaryInfoText,
  level1BinaryNote,
  level6BinaryTask,
  level6BinaryTextBox,
  levelNoInput
} from "../strings";
import { convertToAlternate } from "../helpers";


const LevelNumber = 6;

function generateNumber(currentProgress) {
  const numbers = [120, 82, 254];
  if (currentProgress >= numbers.length) {
    return Math.ceil(Math.random() * 252) + 2; // Generate integer between 2 and 254. (Only even numbers will be used)
  }
  return numbers[currentProgress];
}


const exerciseStyle = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    maxWidth: "80dvh",
    fontFamily: FONT,
    fontSize: "7dvh",
    backgroundColor: COLOR_DARK,
    filter: "drop-shadow(0.2svh 0.2svh 0.6svh rgba(0, 0, 0, 0.5))",
    borderRadius: "0.25em",
    padding: "5dvh",
    marginTop: "8dvh"
};

const textBoxStyle = {
    display: "flex",
    flexWrap: "wrap",
    marginTop: "2dvh",
    width: "60dvh",
    height: "10.5dvh",
    alignItems: "center",
    fontFamily: FONT,
    fontSize: "2dvh",
    backgroundColor: COLOR_DARK,
    filter: "drop-shadow(0.2svh 0.2svh 0.6svh rgba(0, 0, 0, 0.5))",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    borderRadius: "0.5em",
    padding: "2dvh"
  };

const inputStyle = {
    maxWidth: "25dvh",
    fontFamily: BINARY_FONT,
    fontSize: "7dvh",
    boxShadow: "0.9svh 0.9svh 0.6svh rgba(0, 0.5, 0, 0.5)",
};

function Level6Binary({levelProgress, setLevelProgress}) {
  const [number, setNumber] = useState(
    generateNumber(levelProgress[LevelNumber])
  );
  const [solution, setSolution] = useState("");
  const [input, setInput] = useState("");
  const [disableInput, setDisableInput] = useState(false);

  const [message, setMessage] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [buttonText, setButtonText] = useState(buttonCheck);
  const [checkProgress, setCheckProgress] = useState(0);


  useEffect(() => {
    let newNumber = generateNumber(levelProgress[LevelNumber]);
    while (newNumber % 2 !== 0) {
        newNumber = generateNumber(levelProgress[LevelNumber]); // Make sure number is even.
    }
    setNumber(newNumber);
  }, [levelProgress]);

  useEffect(() => {
    setSolution(number / 2);
  }, [number]);

  const hideModal = () => {
    setButtonText(buttonCheck); // Reset button text.
    setShowModal(false);
  };

  const displayModal = () => {
    setButtonText(buttonContinue);
    setShowModal(true);
  };
 
  const checkAnswer = () => {
    const isCorrect = input === convertToAlternate(BINARY_SYSTEM, solution);
    if (checkProgress === 0) {
      if (isCorrect) {
        if (levelProgress[LevelNumber] + 1 === REPEATS) {
          setMessage(levelCorrect + nextLevelUnlocked);
        } else {
          setMessage(levelCorrect);
        }
      } else if (input === "") {
        setMessage(levelNoInput);
      } else {
        setMessage(levelIncorrect);
      }
      setCheckProgress(1);
      displayModal();
    } else {
      if (isCorrect) {
        increaseProgress(levelProgress, setLevelProgress, LevelNumber, BINARY_SYSTEM); // Triggers a useEffect.
      }
      setInput("");
      setMessage("");
      setCheckProgress(0);
      hideModal();
    }
  };

return (
    <div>
      <div
        className="Welcome-label"
        style={{ fontFamily: FONT, fontSize: "4dvh", marginTop: "4dvh" }}
      >
        {level6BinaryTask}
      </div>
      
      {showModal && <div className="checkAnswer-message">{message}</div>}
      <DndProvider options={HTML5toTouch}>
        <div style={{display: "flex", flexDirection: "column", alignItems: "center"}}>
            <div style={exerciseStyle}>
                <span style={{fontFamily: BINARY_FONT}}>{convertToAlternate(BINARY_SYSTEM, number)}</span>&nbsp;&nbsp;:&nbsp;&nbsp;2&nbsp;&nbsp;=&nbsp;&nbsp;
                <input
                className="input-field"
                style={inputStyle}
                type="text"
                value={input}
                maxLength={7}
                disabled={disableInput}
                placeholder=""
                onChange={(e) => {
                    e.target.value = e.target.value.replace(/[^0-1]/g, '');
                    setInput(e.target.value);
                }}
                />
            </div>
            <div style={textBoxStyle}>
                {level6BinaryTextBox}
            </div>
        </div>
        <button
          onClick={checkAnswer}
          className="checkAnswer-button"
        >
          {buttonText}
        </button>
      </DndProvider>
    </div>
  );
}

export default Level6Binary;