import React from "react";
import { COLOR_DARK, COLOR_LIGHT, FONT, NUMBER_OF_SYSTEMS, ROMAN_SYSTEM, BINARY_SYSTEM, MAYA_SYSTEM } from "../defaults";
import { ChooseSystemPopupBinary, ChooseSystemPopupMaya, ChooseSystemPopupRoman, ProgressPopupOkay, ProgressPopupSkip } from "../strings";


const ChooseSystemPopup = ({
  handleClose,
  show,
  whichSystem,
  setWhichSystem,
  children // Text to be displayed is child.
}) => {
  const popupmain = {
    background: COLOR_DARK,
    padding: "6dvh",
    width: "100%",
    height: "100%",
  };

  const popup = {
    position: "fixed",
    top: "0",
    left: "0",
    width: "100%",
    height: "100%",
    backgroundcolor: "rgba(0, 0, 0, 0.5)",
    justifycontent: "center",
    alignitems: "center",
    display: show ? "flex" : "none",
    zIndex: "2",
    fontFamily: FONT,
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
  };

  const buttonStyle = {
    margin: "1dvh",
    padding: "3dvh",
    fontFamily: "Times New Roman, serif",
    color: "white",
    fontSize: "10dvh",
    backgroundColor: COLOR_LIGHT,
    borderRadius: "0.25em",
    border: "none",
    boxShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    textShadow: "0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5)",
    cursor: "pointer"
  }

  // This should be fine since "children" is never user generated
  const formattedText = children.replace(/\n/g, "<br>");

  const changeNumeralSystem = () => {
    setWhichSystem((system) => (system + 1) % NUMBER_OF_SYSTEMS);
    console.log("old system: "+whichSystem);
  };

  const handleChooseRoman = () => {
    setWhichSystem(ROMAN_SYSTEM);
    handleClose(); // Sets show to false, making popup invisible.
  }

  const handleChooseBinary = () => {
    setWhichSystem(BINARY_SYSTEM);
    handleClose(); // Sets show to false, making popup invisible.
  }

  const handleChooseMaya = () => {
    setWhichSystem(MAYA_SYSTEM);
    handleClose(); // Sets show to false, making popup invisible.
  }

  return (
    <div style={popup}>
      <section className="popup-main" style={popupmain}>
        {<h1 dangerouslySetInnerHTML={{ __html: formattedText }}></h1>}
        <br></br>
        <br></br>
        <button
          onClick={handleChooseRoman}
          className="button"
          style={buttonStyle}
        >
          {ChooseSystemPopupRoman}
        </button>
        <div></div>
        <button
          onClick={handleChooseBinary}
          className="button"
          style={buttonStyle}
        >
          {ChooseSystemPopupBinary}
        </button>
        <button
          onClick={handleChooseMaya}
          className="button"
          style={buttonStyle}
        >
          {ChooseSystemPopupMaya}
        </button>
      </section>
    </div>
  );
};

export default ChooseSystemPopup;
